$rubik: 'Rubik', sans-serif;
$redHatText: 'Red Hat Text', sans-serif;
$redHatDisplay: 'Red Hat Display', sans-serif; 
$red: #FF0000;
$blue: #408CFF;

.header {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  margin: 0 auto;
  transition: .5s ease-in-out;
  position: fixed;
  @media (max-width: 600px) {
    padding: 10px 0;
    transition: background-color .5s ease-in-out;
  }
  width: 100%;
  background: white;
  justify-content: space-around;
  .header-items {
    width: 100%;
    display: grid;
    grid-template-columns: 32vw 36vw 32vw;
    @media (max-width: 600px) {
      display: flex;
      align-items: center;
      height: 100vh;
      justify-content: space-around;
      transition: .2s ease-in-out;
      flex-direction: column;
    }
    @media (min-width: 825px) and (max-width: 1200px) {
      grid-template-columns: 25vw 50vw 25vw;
    }
    @media (min-width: 600px) and (max-width: 824px) {
      grid-template-columns: auto auto 20vw;
    }
    .links {
      grid-column-start: 2;
      margin: 0 auto;
      display: flex;
      align-items: center;
      @media (max-width: 600px) {
        display: flex;
        margin: 0;
        flex-direction: column;
      }
      grid-column-end: span 1;
      .link-wrapper {
        @media (min-width: 1212px) and (max-width: 1411px) {
          padding: 10px 20px;
        }
        padding: 10px 30px;
        transition: .3s ease-in-out;
        :hover {
          border-bottom: 1px solid black;
        }
        display: inline;
        a {
          font-size: 18px;
          color: inherit;
          line-height: 20px;
          font-weight: 500;
          text-decoration: none;
        }
      }
    }
  }
}

.header-toggle {
  width: 109px;
  height: 49px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  transition: .2s ease-in-out;
  background: rgba(196, 196, 196, .2);
  border-radius: 30.5px;
}

.toggle-wrapper {
  display: flex;
  @media (max-width: 600px) {
    padding: 10px 30px;
  }
  align-items: center;
  justify-content: center;
  grid-column-start: 3;
  grid-column-end: span 1;
}

.toggle-circle {
  width: 45px;
  height: 45px;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  background: rgba(196, 196, 196, 0);
  border-radius: 50%;
}

@keyframes toggle {
  0% {
    background-color: rgba(196, 196, 196, 0);
    ::before {
      content: '';
      width: 45px;
      height: 45px;
      display: flex;
      cursor: pointer;
      justify-content: center;
      align-items: center;
      background: rgba(196, 196, 196, 0);
      border-radius: 50%;
      margin-left: 40px;
    }
  }
  50% {
  }
  100% {
    background-color: rgba(156, 155, 155, .5);
  }
}

.toggle-circle.active {
  animation: toggle .5s forwards;
  background: rgba(156, 155, 155, .5);
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.55);
}

.circle-image {
  max-width: 25px;
  max-height: 25px;
}

.langRadio {
  display: none;
}

.centered-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-form {
  display: flex;
  min-width: 300px;
  width: 310px;
  max-width: 500px;
  flex-direction: column;
}

.name-input, .contact-button {
  width: 200px;
  align-self: flex-start;
  resize: none;
}

.contact-button {
  cursor: pointer;
  transition: .235s ease-in-out;
}

.contact-button:hover {
  background: $blue;
}

.textarea-input {
  width: 200px;
  align-self: flex-end;
}

.email-input {
  width: 200px;
  align-self: center;
}

.textarea-input, .email-input {
  padding: 0;
  margin: 0;
  
}

.name-input {
  max-width: 190px;
}

.email-input, .textarea-input {
  max-width: 330px;
}


.textarea-input, .email-input, .name-input {
  text-indent: 15px;
}

.name-input, .email-input, .textarea-input, .contact-button {
  width: 275px;
  height: 49px;
  margin: 20px 0;
  font-family: $redHatText;
  font-size: 18px;
  background: #FFFFFF;
  border: 1px solid #408CFF;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 28.5px;
}

.contact-section {
  background: $blue;
  min-height: 600px;
  padding-bottom: 100px;
}

.contact-header {
  font-family: $redHatText;
  font-style: normal;
  text-align: center;
  font-weight: normal;
  font-size: 48px;
  line-height: 64px;
  color: #FFFFFF;
  text-shadow:
		-1px -1px 0 #FFFFFF,
		1px -1px 0 #FFFFFF,
		-1px 1px 0 #FFFFFF,
		1px 1px 0 #FFFFFF;
}

.textarea-input {
  resize: none;
  padding: 10px;
  text-indent: 10px;
  min-height: 160px;
  border-radius: 11px;
}

.contact-button {
  color: white;
  font-size: 24px;
  width: 220px;
  border: 4px solid white;
  height: 75px;
  font-weight: 900;
  border-radius: 38px;
  background-color: $red;
}

.about-header {
  font-size: 48px;
  text-transform: uppercase;
  font-weight: 900;
  text-shadow:
		-1px -1px 0 #FFFFFF,
		1px -1px 0 #FFFFFF,
		-1px 1px 0 #FFFFFF,
		1px 1px 0 #FFFFFF;
}

.about-paragraph {
  font-size: 24px;
  max-width: 50vw;
  @media (max-width: 600px) {
    max-width: 300px;
  }
  font-weight: 700;
}

.about {
  color: white;
  text-align: center;
  font-family: $redHatText;
  background-color: $blue;
}

.about img {
  max-width: 50%;
  max-height: 50%;
}

.project-container {
  display: flex;
  padding: 35px 0;
  flex-wrap: wrap;
  justify-content: center;
}

.project-header {
  @media (max-width: 1049px) {
    flex-direction: column;
  }
  display: flex;
  align-items: center;
}

.project-text {
  padding-left: 3vw;
  @media (max-width: 600px) {
    padding-left: 0;
  }
}

.project-header-date {
  @media (max-width: 1049px) {
    margin-left: 0;
  }
  padding: 0 20px;
  border-radius: 41px;
  font-size: 14px;
  color: $blue;
  margin-left: 20px;
  border: 1px solid $blue;
}

.project-date {
  margin: 5px 0;
  line-height: 14px;
}

.project-paragraph-text {
  font-size: 18px;
  max-width: 500px;
  @media (max-width: 520px) {
    padding: 0 10px;
  }
}

.project-header-name {
  line-height: 30px;
}

.projects-container {
  margin: 0 auto;
  text-align: center;
}

.projects-header {
  text-transform: uppercase;
  font-size: 48px;
  font-family: $redHatText;
  letter-spacing: 5px;
  font-weight: 500;
  line-height: 48px;
  margin: 0;
  margin-top: 40px;
}

.project-list {
  padding: 70px 0;
}

.start-container-wrapper  {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  max-height: 1000px;
}

.start-buttons {
  display: flex;
  justify-content: center;
}

.blue-detail {
  color: $blue;
}

.blue-detail, .red-detail {
  padding: 0 calc(5px + (20 - 5) * ((100vw - 300px) / (1600 - 300)));;
}

.start-header {
  font-family: $rubik;
  font-size: calc(24px + (48 - 24) * ((100vw - 300px) / (1600 - 300)));
}

.start-button {
  padding: 5px 20px;
  margin: 10px;
  font-size: 18px;
  cursor: pointer;
  outline: none;
  border: none;
  background: white;
  border-radius: 35px; 
}

.start-button a {
  color: inherit;
  text-decoration: none;
}

.github {
  border: 2px solid black;
  transition: .5s ease-in-out;
}

.github:hover {
  border: 2px solid $red;
  color: $red;
} 

.contact {
  color: $blue;
  transition: .5s ease-in-out;
  border: 2px solid $blue; 
}

.contact:hover {
  border: 2px solid $red;
  color: $red;
} 

.red-detail {
  color: $red;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.heading-header {
  font-size: 24px;
  animation: fade .5s forwards;
  transition: .2s ease-in-out
}

.cross-fade-leave {
  opacity: 1;
}
.cross-fade-leave.cross-fade-leave-active {
  opacity: 0;
  transition: opacity 1s ease-in;
}

.cross-fade-enter {
  opacity: 0;
}
.cross-fade-enter.cross-fade-enter-active {
  opacity: 1;
  transition: opacity 1s ease-in;
}

.cross-fade-height {
  transition: height 0.5s ease-in-out;
}

.container {
  display: flex;
}

.car-display-child-wrapper {
  width: 600px;
  height: 300px;
}

.car-display-child-wrapper > img {
  max-height: 100%;
  max-width: 100%;
}

.project-img {
  max-width: 500px;
  max-height: 370px;
}

@media screen and (max-width: 750px) {
  .project-img {
    max-width: 250px;
    max-height: 150px;
  } 
  
}


.bar-wrapper {
  width: 30px;
  height: 30px;
  padding: 5px;
  align-self: flex-end;
  padding-right: 20px;
  background: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.bar-1, .bar-2, .bar-3 {
  height: 3px;
  margin: 2px;
  width: 30px;
  transform-origin: center center;
}

@keyframes bar {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(225deg);
    margin: -3px;
  }
}


@keyframes barN {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(-225deg);
    margin: 0;
  }
}

@keyframes disapp {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.bar-1.anim {
  animation: bar .2s forwards;
}

.bar-2.anim {
  animation: barN .2s forwards;
}

.bar-3.anim {
  animation: disapp .2s forwards;
}

@keyframes out {
  0% {
    transform: rotate(225deg);
    margin: -3px;
  }
  100% {
    transform: rotate(0);
    margin: 3px;
  }
}

@keyframes outN {
  0% {
    transform: rotate(-225deg);
    margin: -3px;
  }
  100% {
    transform: rotate(0);
    margin: 3px;
  }
}

@keyframes app {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.bar-1.out {
  animation: out .2s forwards;
}

.bar-2.out {
  animation: outN .2s forwards;
}

.bar-3.out {
  animation: app .2s forwards;
}

.project-buttons {
  text-align: left;
  @media (max-width: 1049px) {
    text-align: center;
  }
}

.action-button {
  box-sizing: border-box;
  border: none;
  padding: 0 20px;
  margin: 10px;
  transition: .2s ease-in-out;
  cursor: pointer;
  height: 41px;
  min-width: 100px;
  border-radius: 20.5px;
}

.action-button.source {
  background: $blue;
  border: 1px solid $blue;
  color: white;
}

.action-button.req {
  background: rgba(64, 140, 255, 0.67);
  color: white;
  cursor: not-allowed;
  border: 1px solid white;
}

.action-button.demo {
  background: white;
  color: $blue;
  border: 1px solid $blue;
}

.action-link {
  color: inherit;
  font-size: 16px;
  font-family: $redHatDisplay;
  letter-spacing: 1px;
  text-decoration: none;
}

.action-button.req .action-link {
  cursor: not-allowed;
}

.action-button.source:hover {
  background: white;
  color: $blue;
}
.action-button.demo:hover {
  color: white;
  background: $blue;
  border: 1px solid white;
}

.blue-detail:last-child {
  padding-left: 0;
}

.square-holder {
  display: flex;
  margin-top: 200px;
  justify-content: center;
}

.square {
  width: 22px;
  height: 22px;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: #4b9cdb;
}

.square {animation: loading 1.5s cubic-bezier(.17,.37,.43,.67) infinite;}

@keyframes loading {
  0% {transform: translate(-70px,0) rotate(0deg);}
  50% {transform: translate(70px,0) rotate(360deg);}
  100% {transform: translate(-70px, 0) rotate(0deg);}
}


.message-sent {
  color: white;
}

button {
  outline: none;
}

.line {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background-color: white;
}

.c-loading .line:nth-last-child(1) {animation: loadingC .6s .1s linear infinite;}
.c-loading .line:nth-last-child(2) {animation: loadingC .6s .2s linear infinite;}
.c-loading .line:nth-last-child(3) {animation: loadingC .6s .3s linear infinite;}

@keyframes loadingC {
  0% {transform: translate(0,0);}
  50% {transform: translate(0,15px);}
  100% {transform: translate(0,0);}
}

.message-sent-header {
  color: white;
}

.sent-wrapper {
  margin: auto;
}

.nf-wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.nf-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

.nf-img {
  max-width: 80%;
  max-height: 100%;
}

.nf-header {
  color: $blue;
  font-size: calc(20px + (30 - 20) * ((100vw - 300px) / (1600 - 300)));
  text-align: center;
  font-family: $rubik;
}